import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import logo from '../logo-signaltopreview.jpg';
import './Header.css';

const Header = () => {
  const navigate = useNavigate();
  const [isMenuOpen, setIsMenuOpen] = useState(false); // State  iu khin menu

  const handleCompareClick = () => {
    navigate('/compare-providers');
    setIsMenuOpen(false); // ng menu sau khi chn
  };

  const handleBoardClick = () => {
    navigate('/');
    setIsMenuOpen(false); // ng menu sau khi chn
  };

  const handleCalculatorClick = () => {
    navigate('/profit-calculator');
    setIsMenuOpen(false); // ng menu sau khi chn
  };

  const toggleMenu = () => {
    setIsMenuOpen(!isMenuOpen); // M/ng menu
  };

  return (
    <header className="header">
      <div className="logo">
        <img src={logo} alt="SignalTopReview Logo" className="logo-image" />
        <span>SignalTopReview</span>
      </div>

      {/* Nt hamburger cho thit b di ng */}
      <button className="hamburger" onClick={toggleMenu}>
        <span className="hamburger-icon">&#9776;</span> {/* Biu tng hamburger */}
      </button>

      {/* Menu iu hng */}
      <nav className={`nav ${isMenuOpen ? 'open' : ''}`}>
        <ul>
          <li onClick={handleBoardClick} style={{ cursor: 'pointer' }}>
            Provider Board
          </li>
          <li onClick={handleCompareClick} style={{ cursor: 'pointer' }}>
            Compare
          </li>
          <li onClick={handleCalculatorClick} style={{ cursor: 'pointer' }}>
            Profit Calculator
          </li>
          <li>More</li>
        </ul>
      </nav>
    </header>
  );
};

export default Header;